@import url(https://fonts.googleapis.com/css?family=PT+Sans+Narrow&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.Product {
  width: 200px;
  min-width: 200px;
  height: 200px;
  display: inline-block;
  text-align: center;
  border: black solid 2px;
  margin: 15px;
}

.cartBox {
  margin: 5px;
  padding: 15px;
  height: 60%;
  display: flex;
}
.productImage {
  height: 90%;
  width: 100%;
  position: relative;
  background: lightgray;
}

.discount-tag {
  position: relative;
  background-color: #00cc00;
  width: 15%;
  padding: 1px;
  height: 20px;
}
.header {
  display: flex;
  height: 100px;
  position: relative;
}

.column {
  margin-top: 10px;
  height: 30px;
}

/* Should be removed. Only for demonstration */
.left {
  text-align: left;
  margin-left: 3px;
  width: 25%;
}

.right {
  text-align: right;
  position: absolute;
  margin-right: 3px;
  right: 0px;
  float: right;
}

.middle {
  text-align: center;
  width: 50%;
}

button {
  width: 100px;
  height: 40px;
  left: 30px;
}

.cart-info {
  background: aquamarine;
  text-align: center;
  padding: 10px;
}

.inline-display {
  display: inline;
}
a {
  border: 0 none;
  outline: 0;
  text-decoration: none;
}

strong {
  font-weight: bold;
}

p {
  margin: 0.75rem 0 0;
}

h1 {
  font-size: 0.75rem;
  font-weight: normal;
  margin: 0;
  padding: 0;
}

img, .basket-module, .basket-labels, .basket-product {
  width: 100%;
}

.basket, .basket-module, .basket-labels, .item, .price, .quantity, .subtotal, .basket-product, .product-image, .product-details {
  float: left;
}

.price:before, .subtotal:before, .subtotal-value:before, .total-value:before, .promo-value:before {
  content: "$";
}

.hide {
  display: none;
}

main {
  clear: both;
  font-size: 0.75rem;
  margin: 0 auto;
  overflow: hidden;
  padding: 1rem 0;
  width: 960px;
}

.basket, aside {
  padding: 0 1rem;
  box-sizing: border-box;
}

.basket {
  width: 70%;
}

.basket-module {
  color: #111;
}

label {
  display: block;
  margin-bottom: 0.3125rem;
}

.promo-code-field {
  border: 1px solid #ccc;
  padding: 0.5rem;
  text-transform: uppercase;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  width: 48%;
  -o-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}
.promo-code-field:hover, .promo-code-field:focus {
  border: 1px solid #999;
}

.promo-code-cta {
  border-radius: 4px;
  font-size: 0.625rem;
  margin-left: 0.625rem;
  padding: 0.6875rem 1.25rem 0.625rem;
}

.basket-labels {
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  margin-top: 1.625rem;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  color: #111;
  display: inline-block;
  padding: 0.625rem 0;
}
li.price:before, li.subtotal:before {
  content: "";
}

.item {
  width: 55%;
}

.price, .quantity {
  width: 15%;
}

.subtotal {
  width: 15%;
  text-align: right;
}

.remove {
  bottom: 1.125rem;
  float: right;
  position: absolute;
  right: 0;
  text-align: right;
}

.remove-button {
  background-color: transparent;
  color: #777;
  float: none;
  width: 20px;
  height: 20px;
  text-decoration: underline;
  text-transform: uppercase;
}

.item-heading {
  padding-left: 4.375rem;
  box-sizing: border-box;
}

.basket-product {
  border-bottom: 1px solid #ccc;
  padding: 1rem 0;
  position: relative;
}

.product-details {
  width: 65%;
}

.product-frame {
  height: 30px;
  display: flex;
  border: gray 1px solid;
  padding: 6px;
  font-size: 20px;
  width: 50%;
}

.product-details {
  padding: 0 1.5rem;
  box-sizing: border-box;
}

.quantity-field {
  background-color: #ccc;
  border: 1px solid #aaa;
  border-radius: 4px;
  font-size: 0.625rem;
  padding: 2px;
  width: 3.75rem;
}

aside {
  float: right;
  position: relative;
  width: 30%;
}

.summary {
  border: 1px solid #aaa;
  padding: 1rem;
  position: fixed;
  width: 250px;
  box-sizing: border-box;
}

.summary-total-items {
  color: #666;
  font-size: 0.875rem;
  text-align: center;
}

.summary-subtotal, .summary-total {
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  clear: both;
  margin: 1rem 0;
  overflow: hidden;
  padding: 0.5rem 0;
}

.subtotal-title, .subtotal-value, .total-title, .total-value, .promo-title, .promo-value {
  color: #111;
  float: left;
  width: 50%;
}

.summary-promo {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.promo-title {
  float: left;
  width: 70%;
}

.promo-value {
  color: #8B0000;
  float: left;
  text-align: right;
  width: 30%;
}

.summary-delivery {
  padding-bottom: 3rem;
}

.subtotal-value, .total-value {
  text-align: right;
}

.total-title {
  font-weight: bold;
  text-transform: uppercase;
}

.summary-checkout {
  display: block;
}

.checkout-cta {
  display: block;
  float: none;
  font-size: 0.75rem;
  text-align: center;
  text-transform: uppercase;
  padding: 0.625rem 0;
  width: 100%;
}

.summary-delivery-selection {
  background-color: #ccc;
  border: 1px solid #aaa;
  border-radius: 4px;
  display: block;
  font-size: 0.625rem;
  height: 34px;
  width: 100%;
}
